import React from 'react';
import SearchIcon from "@material-ui/icons/Search";
import ShoppingBasketIcon from "@material-ui/icons/ShoppingBasket";
import { Link } from "react-router-dom";
import { useStateValue } from "../StateProvider";
import { auth } from "./auth/firebase";



function Nav(props){
  

	const user = "Guest"
  const handleAuthenticaton = () => {
    if (user) {
      auth.signOut();
    }
  }

    return(
              <nav className="navbar navbar-light tortinental-nav">
                 <a className="navbar-brand title-text" href="#">
                     <img src={props.image} width="20" height="30" className="d-inline-block align-top mr-1" alt=""></img>
                         {props.site}
                </a>
				
					<Link to={!user && '/login'}>
							<div onClick={handleAuthenticaton} className="header__option" >
									<span className="header__optionLineTwo" >  {!user ? 'Guest' : 'Hello Guest'}  </span>
						</div>
				</Link>
               </nav>
    );
}
export default Nav;
